import fs from 'fs';
import path from 'path';

/**
 * Interface Definitions
 */
interface Directories {
  root: string;
  subDir?: string;
}

interface Paths {
  name: string;
  path: string;
  dir: string;
  scss: string;
  demo?: string;
  config: {
    name: string;
    type: string;
    default: {
      name: string;
    };
    variants: {
      name: string;
    }[];
  };
}

/**
 * @function function to list all given component folders
 * @author Experience One AG
 * @copyright Robert Bosch GmbH
 *
 * @param   {string} name     Name of the component
 * @param   {string} path     Path of the component
 * @param   {string} dir      directory of the component
 * @param   {string} scss     scss file of the component
 * @param   {object} config   config file of the component
 * @description component will only be listed if a .tsx, .scss and .json is given
 * folders will be searched recursively
 */
const readConfigurationFiles = ({
  root,
  subDir = '',
}: Directories): Paths[] => {
  const folders = fs.readdirSync(root).filter(item => {
    return (
      fs.existsSync(path.join(root, item)) &&
      fs.lstatSync(path.join(root, item)).isDirectory()
    );
  });
  let folderPaths = [];

  folders.forEach(folder => {
    const route = path.join(root, folder, folder);
    const demoPath = path.join(root, folder, 'demo.ts');

    if (
      fs.existsSync(`${route}.tsx`) &&
      fs.existsSync(`${route}.scss`) &&
      fs.existsSync(`${route}.json`)
    ) {
      // assign full paths to each component object
      const scss = fs.readFileSync(`${route}.scss`).toString();
      const config = JSON.parse(fs.readFileSync(`${route}.json`).toString());
      let demo = null;

      if (fs.existsSync(demoPath)) {
        demo = fs.readFileSync(demoPath).toString();
      }

      folderPaths = [
        ...folderPaths,
        {
          name: folder,
          path: route,
          dir: path.join(subDir, folder),
          scss,
          demo,
          config,
        },
      ];
    } else {
      folderPaths = [
        ...folderPaths,
        ...readConfigurationFiles({
          root: `${root}/${folder}`,
          subDir: path.join(subDir, folder),
        }),
      ];
    }
  });

  return folderPaths;
};

export default readConfigurationFiles;
