import fs from 'fs';
import path from 'path';
import prettier from 'prettier/standalone';
import parserHTML from 'prettier/parser-html';

import { Markup } from './compileTemplates';
import { newLine, frontmatterBlock } from './variables';

interface Input {
  markup: Markup[];
  name: string;
  dir: string;
  scss: string;
  rootPath: string;
  demo?: string;
}

/**
 * @name    writeGuideFile
 * @author Experience One AG
 * @copyright Robert Bosch GmbH
 *
 * @param   {array} markup    Array of variations of a given component
 * @param   {string} name     Name of the component
 * @param   {string} dir      directory of the component
 * @param   {string} scss     parsed scss og the component
 * @param   {string} rootPath path to the root folder of the project
 * @description
 * writes all available content into one guide file for each component
 */
const writeGuideFile = ({
  markup,
  name,
  dir,
  scss,
  demo,
  rootPath,
}: Input): string[] => {
  let writtenFiles = [];

  const outputPath: string = path.join(
    path.join(rootPath, 'src', 'pages', 'guide', dir),
  );

  // to run proper on windows machines we need to adjust the path
  const fixedDir = dir.replace(/\\/g, '/');

  // loop over the given markup to generate each given variant
  markup.forEach(({ variant, variantPath, html }, order) => {
    let content = '';
    content += frontmatterBlock({
      path: `/${fixedDir}/guide`,
      type: 'guide',
      level: `${dir.split(path.sep)[0]}`,
      title: name,
      variant,
      order: order + 1,
    });

    const prettyHTML = prettier.format(html, {
      parser: 'html',
      printWidth: 80,
      htmlWhitespaceSensitivity: 'ignore',
      plugins: [parserHTML],
    });

    // content += newLine();
    content += '```html\n';
    content += `${prettyHTML}`;
    content += '```\n';

    // create new directory
    fs.mkdirSync(outputPath, { recursive: true });
    const fileOutputPath = path.join(outputPath, `${variantPath}.md`);
    let currentContent = '';

    if (fs.existsSync(fileOutputPath)) {
      currentContent = fs.readFileSync(fileOutputPath).toString();
    }

    writtenFiles = [...writtenFiles, fileOutputPath];

    if (currentContent !== content) {
      fs.writeFileSync(fileOutputPath, content);
    }
  });

  /**
   * write SCSS markdown file for component
   */

  let scssFile = '';

  scssFile += frontmatterBlock({
    path: `/${fixedDir}/styles`,
    type: 'styles',
    level: `${dir.split(path.sep)[0]}`,
    title: name,
  });

  scssFile += '```scss';
  scssFile += newLine();
  scssFile += `${scss}`;
  scssFile += '```';
  scssFile += newLine();

  // create new Directory and write content as .md file
  fs.mkdirSync(outputPath, { recursive: true });
  const scssFileOutputPath = path.join(outputPath, `styles.md`);
  let currentSCSSContent = '';

  if (fs.existsSync(scssFileOutputPath)) {
    currentSCSSContent = fs.readFileSync(scssFileOutputPath).toString();
  }

  writtenFiles = [...writtenFiles, scssFileOutputPath];

  if (currentSCSSContent !== scssFile) {
    fs.writeFileSync(scssFileOutputPath, scssFile);
  }

  /**
   * write optional given demo markdown file of component
   */
  if (demo) {
    let demoFile = '';

    demoFile += frontmatterBlock({
      path: `/${fixedDir}/demo`,
      type: 'demo',
      level: `${dir.split(path.sep)[0]}`,
      title: name,
    });

    demoFile += '```ts';
    demoFile += newLine();
    demoFile += `${demo}`;
    demoFile += '```';
    demoFile += newLine();

    // create new Directory and write content as .md file
    fs.mkdirSync(outputPath, { recursive: true });
    const demoFileOutputPath = path.join(outputPath, `demo.md`);
    let currentDemoContent = '';

    if (fs.existsSync(demoFileOutputPath)) {
      currentDemoContent = fs.readFileSync(demoFileOutputPath).toString();
    }

    writtenFiles = [...writtenFiles, demoFileOutputPath];

    if (currentDemoContent !== demoFile) {
      fs.writeFileSync(demoFileOutputPath, demoFile);
    }
  }

  return writtenFiles;
};

export default writeGuideFile;
