/**
 * This file contains static building blocks who write the .md-files
 */

/**
 * Interface defines the FrontmatterBlocks of the written .md-files
 */
interface FrontmatterBlock {
  (data: {
    path: string;
    type: string;
    level: string;
    title: string;
    variant?: string;
    order?: number;
    overview?: boolean;
    overviewColumn?: number;
    overviewColumnOrder?: number;
  }): string;
}

interface NewLine {
  (dashLength?: 0 | 3): string;
}

interface ComponentBackground {
  (dummyBackground: string, html: string);
}

/**
 * @name    newLine
 * @author Experience One AG
 * @copyright Robert Bosch GmbH
 *
 * @param   {number} dashLength   Numeric value of how many dashes should appear before "\n" is set. Defaults to 0.
 * @return  {string}              Newline String with dashes
 * @description
 * Generates a Newline String with the desired amount of dashes ("-")
 */
export const newLine: NewLine = (dashLength = 0) => {
  const dashString = dashLength > 0 ? '-'.repeat(dashLength) : '';
  return `${dashString}\n`;
};

/**
 * @name    componentBackground
 * @author Experience One AG
 * @copyright Robert Bosch GmbH
 *
 * @param   {string} dummyBackground   The type of dummy background that should apply
 * @param   {string} html   The appearance of the component
 * @return  {string}              a component wrapped by a background
 * @description
 * Generates a component wrapped by a background
 */
export const componentBackground: ComponentBackground = (
  dummyBackground,
  html,
) => {
  const wrappedComponent = `<div class="frontend-kit__example-wrapper -${dummyBackground}">
  ${html}

  </div>`;
  return `${wrappedComponent}\n`;
};

/**
 * @name    frontmatterBlock
 * @author Experience One AG
 * @copyright Robert Bosch GmbH
 *
 * @param   {object} data   Object with Key-Value-Pairs of frontmatter-Data
 * @return  {string}        String with Formatted frontmatter-Data
 * @description
 * Formats Object of Key-Value-Pairs to frontmatter-Data
 */
export const frontmatterBlock: FrontmatterBlock = data => {
  let content = newLine(3);

  Object.entries(data).forEach(([key, value]) => {
    if (value) {
      if (typeof value === 'number') {
        content += `${key}: ${value}`;
        content += newLine();
      } else {
        content += `${key}: "${value}"`;
        content += newLine();
      }
    }
  });

  content += newLine(3);
  content += newLine();

  return content;
};
