import path from 'path';
import rimraf from 'rimraf';
import * as React from 'react';
import { renderToStaticMarkup } from 'react-dom/server';
import * as Components from '../src/frontend-kit/components';

import readConfigurationFiles from './readConfigurationFiles';
import readMarkdownFiles from './readMarkdownFiles';
import writeGuideFiles from './writeGuideFile';
import writePreviewFiles from './writePreviewFiles';

// interface definitions
export interface Markup {
  // the human-readable component name
  name: string;
  // the variant identifier
  variant: string;
  // the target path for this variant's markup
  variantPath: string;
  // the rendered markup
  html: string;
  // optional background identifier
  dummyBackground?: string;
  // optional override to render another component instead
  demonstrator?: string;
  // indicates if variant should be displayed in overview. If one of the two attributes is set, variant is displayed.
  overview?: {
    // it is possible to arrange the items on the overview page using a grid system
    // specifies the column in which the variant should be displayed
    column: number;
    // specifies the row order within the column
    columnOrder: number;
  };
}

// root path for all components
const rootPath: string = path.resolve();
const frontendKitPath: string = path.join(rootPath, '/src/frontend-kit');
const previewPath: string = path.join(rootPath, '/src/pages/preview');
const guidePath: string = path.join(rootPath, '/src/pages/guide');

// look for all components
const componentConfigurations = readConfigurationFiles({
  root: frontendKitPath,
});
const currentMarkdownFiles = [
  ...readMarkdownFiles({ root: previewPath }),
  ...readMarkdownFiles({ root: guidePath }),
];
let newMarkdownFiles = [];

const missingComponents = [];

/**
 * @function function to write all .md-files
 * @author Experience One AG
 * @copyright Robert Bosch GmbH
 *
 * @description map over all given component configurations
 * write preview .md file for each variant of each component
 * write guide .md file for each component
 *
 * the purpose of this step is to generate all .md files we need to
 * build the preview site of the frontend-kit
 */
componentConfigurations.forEach(({ name, config, scss, demo, dir }) => {
  const { variants = [] } = config;

  const markup: Markup[] = [];

  [config.default, ...variants].forEach((variant: Markup) => {
    /**
     * if there is a demonstrator, render instead of the default component
     * also, ignore any props from configuration
     */
    let componentName = variant.demonstrator || name;
    /**
     * make first character upper case according to the name of components
     */
    componentName =
      componentName.charAt(0).toUpperCase() + componentName.slice(1);

    const component = Components[componentName];
    const props = { ...variant };

    if (!component) {
      if (!missingComponents.includes(componentName)) {
        missingComponents.push(componentName);
        console.warn(
          `Tried to render an unknown react component ${componentName} for component ${name} - skipping`,
        );
      }

      return;
    }
    markup.push({
      name,
      variant: variant.name,
      variantPath: variant.name
        .replace(/\s/g, '-')
        .replace(/,/g, '')
        .toLowerCase(),
      html: renderToStaticMarkup(React.createElement(component, props)),
      dummyBackground: variant.dummyBackground,
    });
  });

  newMarkdownFiles = [
    ...newMarkdownFiles,
    ...writeGuideFiles({
      markup,
      name: config.name,
      dir,
      scss,
      rootPath,
      demo,
    }),
    ...writePreviewFiles({ markup, name: config.name, dir, rootPath }),
  ];
});

// clean up old files, which are not longer generated
let deleted = [];

currentMarkdownFiles.forEach(entry => {
  if (newMarkdownFiles.indexOf(entry) === -1) {
    deleted = [...deleted, entry];
  }
});

deleted.forEach(async item => rimraf.sync(item));
