import fs from 'fs';
import path from 'path';

/**
 * Interface Definitions
 */
interface Directories {
  root: string;
  subDir?: string;
}

interface Paths {
  name: string;
}

/**
 * @function function to list all given markdown files
 * @author Experience One AG
 * @copyright Robert Bosch GmbH
 *
 * @description folders will be searched recursively
 */
const readMarkdownFiles = ({ root, subDir = '' }: Directories): Paths[] => {
  
  if(!fs.existsSync(root)) {
    return [];
  }

  const entries = fs.readdirSync(root).filter(item => {
    return fs.existsSync(path.join(root, item));
  });

  let markdownPaths = [];

  entries.forEach(entry => {
    const entryPath = path.join(root, entry);
    const entryExtension = path.extname(entryPath);

    if (fs.lstatSync(entryPath).isDirectory()) {
      markdownPaths = [
        ...markdownPaths,
        ...readMarkdownFiles({
          root: `${root}/${entry}`,
          subDir: path.join(subDir, entry),
        }),
      ];
    } else if (fs.existsSync(entryPath) && entryExtension === '.md') {
      markdownPaths = [...markdownPaths, entryPath];
    }
  });

  return markdownPaths;
};

export default readMarkdownFiles;
